.main-header{
  background: colorAlpha(lime,.3);
  height: var(--header-height);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 100;
  &.fixed{
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
  }
  h1{
    text-indent: -100000;
    display: none;
  }
  a.logo{
    width: 26.04vw;
    max-width: 500px;
    min-width: 200px;
  }
  .toggle-menu{
    $size: 2rem;
    position: absolute;
    display: block;
    width: $size;
    height: $size;
    right: 1em;
    top: 50%;
    transform: translateY(-50%);
    z-index: 100;
    @include from($bp){
      width: $size*1.25;
      height: $size*1.25;
      right: 2.60vw;
    }
  }
}

.logo-container{
  background: $body-bg;
  height: var(--header-height);
  position: relative;
  z-index: 100;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}



