.Pastel {
  .section-top {
    display: flex;
    justify-content: flex-end;
  }
}
.section-top-grid{
  width: calc(100% - var(--gutter));
  display: grid;
  grid-template-columns: 1fr 55%;
  grid-column-gap: calc(var(--gutter) * 2);

  .mobile &{
    display: block;
    width: 100%;
    padding: 0 1em;
  }
}
.section-top{
  margin-bottom: 27.7vh;
}
.mobile .section-top  {
  margin-bottom: 1em;
}
.section-top-text{
  display: flex;
  flex-direction: column;
  justify-content: center;
  p{
    font-size: 1.6vw;
  }
  .mobile &{
    margin-bottom: 1em;
    p{
      font-size: 1em;
    }
  }
}
.section-top-img{
  .mobile &{

  }
}
.formulario {
  width: 100%;
  max-width: $max-width;
  padding: 0 1em;
  margin: 0 auto;
  font-size: 1.6vw;

  .mobile & {
    font-size: 1em;
  }

  .input-group {
    position: relative;
    margin-bottom: 1em;

    .mobile & {
      margin: 1em 0;
    }
    label{
      margin-bottom: 1em;
      line-height: 1;
      padding-left: .5em;
      font-style: italic;
      font-weight: 700;
    }
  }
}
.box{
  height: 5vh;
  margin-top: .3em;
  border: 1px solid rgba(#000,.8);
  input, select, textarea{
    display: block;
    width: 100%;
    height: 100%;
    background: transparent;
    border: none;
    font-family: inherit;
    font-size: .9em;
    padding-left: .5em;
    font-style: italic;
    &:focus{
      outline: none;
      box-shadow: none;
    }
  }
  &.textarea{
    height: 12vh;
    textarea{
      resize: none;
      padding-top: .1em;
      padding-bottom: .1em;

    }

  }
}
.select select{
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  padding-right: 5.4vh;
}
.select{
  position: relative;
  &::after{
    $size: 5vh;
    content: '';
    display: block;
    position: absolute;
    background: url(../img/arrow-down.png) center / cover no-repeat;
    height: 100%;
    width: $size;
    top: 0;
    right: 0;
    z-index: -1;
  }
}
.form-footer{
  display: flex;
  justify-content: flex-end;
  button{
    border: 1px solid rgba(#000,.8);
    font-size: inherit;
    font-style: italic;
    line-height: 1.8em;
    padding: 0 .8em;
    background: transparent;
    transition: background .3s;
    cursor: pointer;
    &:hover{
      background: darken(#fff,5);
    }
  }
}
