.tabs-container{
  position: relative;
}
.products-menu{
  @extend %lists-style;
  margin-bottom: 2em;
  display: flex;
  justify-content: center;
  li{
    padding: 0 2em;
    position: relative;
    &::after{
      content: '';
      background: var(--color-main);
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0;
      height: 1px;
      width: 7px;
      line-height: 3em;
    }
    &:last-child{
      &::after{
        display: none;
      }
    }
  }
  @include to(700px){
    .mobile &{
      //background: #006dc6;
      li{
        margin: 0 .75em 0 0;
        padding: 0;
        &:last-child{
          margin: 0;
        }
        &::after{
          //display: none;
          width: .25em;
          right: -.5em;
          top: 50%;
          transform: translateY(calc(-50% - 2px));
        }
        a{
          font-size: 3.5vw ;
          line-height: 1;
          display: block;
          //transform: translateY(1px);
        }
      }
    }
  }

  a{
    display: block;
    line-height: 3em;
    text-align: center;
    color: var(--color-main);
    text-transform: uppercase;
    font-size: 1.3em;
    &.active{
      text-decoration: underline;
    }
  }
}
.panels{
  position: relative;
  overflow: hidden;
}
.products-gallery{
  @include grid('.gallery-item', 1 2 3, 3vw);
  font-size: 1.5vw;
  font-style: italic;
  position: absolute;
  left: -1000%;
  top: 0;
  opacity: 0;
  transition: opacity .5s;
  .mobile &{
    font-size: 1.3em;
  }
  &.active{
    position: relative;
    left: 0;
    opacity: 1;
  }
}
.gallery-item{
  cursor: pointer;
  figcaption{
    padding: 2.77vh 0 0;
    text-align: center;
  }
}
