.main-footer{
  position: relative;
  margin-top: auto;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  padding: 20px 0;
  @include from($bp){
    padding: 30px 0;
  }
  &::before{
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 1px;
    width: 80%;
    max-width: 958px;
    background: rgba(#888888,.5);
  }
  ul{
    @extend %lists-style;
    display: flex;
    li{
      margin-right: 1.5em;
      &:last-child{
        margin-right: 0;
      }
    }
    a{
      --size: 2em;
      display: block;
      height: var(--size);
      width: var(--size);
      @include from(m){
        --size: 2.5em
      }
      @include from(l){
        --size: 3em;
      }
    }
  }
}

