.is-mobile{
  position: fixed;
  width: 100%;
  height: 100vh;
  z-index: 10000;
  background: $body-bg;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  @media screen and(orientation: landscape){
    display: flex
  }
  &.show{
    opacity: 1;
  }
  img{
    width: auto;
    height: 60px;
    margin-bottom: 1em;

  }
  p{
    font-size: 1.2em;
    text-align: center;
  }
  &.no-show{
    display: none;
  }
}
