*,
*::before,
*:after {
  box-sizing: border-box;
}

body {
  margin : 0;
  font-family : $font-family;
  font-size: 16px;
  background: $body-bg;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden !important;
}

img{
  display: block;
  max-width: 100%;
  height: auto;
}

a{
  color: inherit;
  text-decoration: none;
  &:hover{
    text-decoration: none;
  }
}

section, article, figure, picture{
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
}

