.main-nav{
  max-width: $max-width;
  margin: 0 auto;

}
.main-menu{
  @include edMenu($bp);
  li{
    position: relative;
    @include to($bp){
      &::before{
        content: '';
        position: absolute;
        width: 100%;
        height: 1px;
        background: rgba(#888888,.5);
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    &:last-child{
      &::before{
        display: none;
      }
    }
  }
  a{
    text-transform: uppercase;
    @include from($bp){
      line-height: 4em;
    }
  }
}
.menu-wraper{
  background: $body-bg;
  position: absolute;
  width: 100%;
  left: 0;
  top: var(--header-height);
  transition: transform .5s;
  transform: translateY(-100%);
  &.show, &.fixed{
    transform: translateY(0);
  }
  &.fixed{
    @include from($bp){
      position: fixed;
      top: 0;
      border-bottom: 1px solid rgba(#888888,.5);
    }
    &::before{
      display: none;
    }
  }
  &::before, &::after{
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    height: 1px;
    width: 80%;
    max-width: 900px;
    background: rgba(#888888,.5);
  }
  &::before{
    top: 0;
    @include to(l){
      width: 100%;
    }
  }
  &::after{
    bottom: 0;
    width: 100%;
    @include from(l){
      display: none;
    }
  }
}

