.sucursal{
  display: grid;
  grid-template-columns: 450px 1fr;
  grid-column-gap: 5.7vw;
  margin-bottom: 9.72vh;
  &__map{
    width: 100%;
    position: relative;
    cursor: pointer;
    background: url("../img/loading.svg") center / 15% no-repeat;
    picture, img{
      border: 1px solid rgba(#888,.5);
      position: absolute;
      width: 100%;
      height: 100%;
      object-position: center;
      object-fit: cover;
      top: 0;
      left: 0;
    }
    iframe{
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      border: none;
    }
  }
  &__data{
    display: flex;
    flex-direction: column;
    justify-content: center;
    h2{
      margin-bottom: 6.5vh;
    }
    p{
      margin-bottom: 0;
      font-size: 1.24vw;
      &.phone{
        margin: 1em 0;
      }
    }
  }
  .mobile &{
    display: block;
    max-width: 500px;
    margin: 0 auto 2em;
    &__map{
    }
    &__data{
      padding: 1em 0;
      display: block;
      h2{
        margin-bottom: .5em;
      }
      p{
        font-size: 1.2em;
      }
    }
  }
}
.Sucursales .main-section{
  margin-top: 9.72vh;
}
.mobile.Sucursales{
  .main-section {
    margin-top: 2em;
  }
  .sucursal{
    position: relative;
    &::after{
      content: '';
      width: 80%;
      height: 1px;
      background: rgba(#888,.5);
      position: absolute;
      left: 50%;
      bottom: -.5em;
      transform: translateX(-50%);
    }
    &:last-child{
      &::after{
        display: none;
      }
    }
  }
}
