.main{
  margin-bottom: 10vh;
}
.mobile{
  .main{
    padding-top: var(--header-height);
  }
  .nomobile{
    display: none;
  }
  .Inicio__grid{
    display: block !important;
  }
}
.section-title{
  font-size: 3.5vw;
  margin-bottom: 1em;
  letter-spacing: 2px;
  .mobile &{
    font-size: 2em;
    letter-spacing: initial;
  }
  &.center{
    text-align: center;
  }
}
.banner{
  display: flex;
  justify-content: center;
  align-items: center;
  &.right, &.left{
    justify-content: flex-end;
    img{
      width: calc(100% - var(--gutter));
    }
  }
  &.left{
    justify-content: flex-start;
  }
}
.container{
  &.full{
    padding: var(--gutter-height) var(--gutter);
  }
}
.Inicio__grid{
  display: grid;
  grid-template-columns: 60% 1fr;
  grid-column-gap: calc(var(--gutter) * 2.5);
  .Inicio__text{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  p{
    font-size: 2vw;
    .mobile &{
      font-size: 1em;
    }
  }
}
