.Nosotros, .Productos{
  .Inicio__grid{
    grid-template-columns: 1fr;
  }
  .container{
    &.full{
      padding: var(--gutter-height) calc(var(--gutter) * 2.5);
    }
  }
}

.grid-text{
  @include from(800px){
    display: grid !important;
    grid-template-columns: 46% 1fr;
    grid-column-gap: calc(var(--gutter) * 3.5);
  }
}
.mobile{
  .text-wrapper1{
    margin-bottom: 1em;
  }
}
