.modal{
  --size : 3em;
  --iconsPos : 5%;
  @include from(m){
    --size: 3em;
    --iconsPos: 20%;
  }
  width: 100%;
  height: 100vh;
  background: rgba(#000,.80);
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-modal);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1em 4em;
  &__img{
    width: auto;
    max-height: 55vh;
  }
  &__close, .arrow{
    position: absolute;
    cursor: pointer;
  }
  p{
    text-align: center;
    color: #fff;
  }
  &__container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: translateX(-2em);
    opacity: 0;
    animation: show .3s .1s forwards;
    @keyframes show {
      to{
        transform: translateX(0);
        opacity: 1;
      }
    }
  }
  p{
    margin: 0;
    width: 100%;
    max-width: 750px;
    @include from(m){
      font-size: 1.2em;
    }
    @include from(l){
      font-size: 1.4em;
    }
  }
  &__name{
    margin-top: 1em !important;
    margin-bottom: 2em !important;
  }
  &__close{
    --size: 2em;
    --iconsPos: 10%;
    width: var(--size);
    height: var(--size);
    right: var(--iconsPos);
    top: 5%;
  }
  .arrow{
    $size: 2em;
    $pos : 10%;
    width: var(--size);
    height: auto;
    top: 50%;
    transform: translateY(-50%);
    .mobile &{
      @include to(l){
        height: var(--size);
      };
    }
    img{
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
    &.left{
      left: var(--iconsPos) ;
    }
    &.right{
      right: var(--iconsPos) ;
    }
    &.noshow{
      display: none;
    }
  }
}
.modal-map .modal__container{
  position: relative;
  width: 100%;
  max-width: 700px;
  height: 60%;
  background: url("../img/loading-light.svg") center / 15% no-repeat;
  iframe{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
}

