// Typography
$font-family: 'Rockwell','sans-serif';
$font-title: 'ChunkFive', serif;
$bp: l;
$body-bg: #FFFFFF;
$color-main: hsl(9,65%,26%);
$gutter: 2.6%;
:root{
  // fonts
  --flight: 300;
  --fNormal: 400;
  --fBold: 700;

  --font-size: 16px;
  --font-lh: 1.2em;

  //layout
  --header-height: 100px;
  --footer-height: 75px;
  --gutter : 1em;
  --gutter-height : 2rem;
  @media screen and(min-width: 640px){
    --header-height: 150px;
    --footer-height: 100px;
    --gutter: 2.6%;
    --gutter-height: #{rem(50px)};
  }
  @media screen and(min-width: 1024px){
    --header-height: 200px;
    --footer-height: 125px;
  }

  //colors
  --alpha : 1;
  --color-main: hsl(9,65%,26%);

  //z-index
  --z-back    : -10;
  --z-normal  : 1;
  --z-tooltip : 10;
  --z-banner: 75;
  --z-banner-c: 90;
  --z-fixed   : 100;
  --z-modal   : 1000;

  //varios
  --gutter    : 1em;
  --trasition: .3s lineal;
}
